* {
    box-sizing: border-box;
    background-color: black;
  }

.app-container {
    display:flex;
    width: 100%;
    height: auto;
}

.main-view {
    display: grid;
    width: 100%;
    grid-template-columns: 0fr 0fr 0fr;
    justify-content: center;
}    

.card {
    margin: 1em;
}

.card-body {
    background-color: white;
}

.card-title {
    background-color: white;
    font-size: 25px;
    font-weight: bold;
}

.card-text {
    background-color: white;
}

